.btn-primary {
  transition: all .2s;
  background: url('/assets/images/right-arrow.png') null null null 10px no-repeat;
  border: 2px solid $green-light;
  color: $green-light;
  float: left;
  font-size: rem(17);
  font-weight: 500;
  margin-top: rem(23);
  padding: rem(12) rem(22) rem(12) rem(60);
  text-decoration: underline;
  text-transform: uppercase;

  &:hover {
    background: url('/assets/images/right-arrow.png') null null null 15px no-repeat;
    color: $sea-shell;
  }

  &:link,
  &:visited,
  &:active {
    color: $green-light;
  }
}
